<template>
  <div>
    <div class="nav fc-grey mar-b-10">
      <div :class="{ 'selected': typenum==1 }" @click="orderStatus1"></div>
      <div :class="{ 'selected': typenum==2 }" @click="orderStatus2"></div>
      <div :class="{ 'selected': typenum==3 }" @click="orderStatus3"></div>
      <div :class="{ 'selected': typenum==4 }" @click="orderStatus4"></div>
    </div>

    <div class="pd-lr-15">
      <div class="bgc daifu-box mar-b-10" v-for="(item,index) in orderlist">
        <div>
          <div
            class="flex-align-items"
            @click="toDetail(item.id)"
            style="padding:15px 0 10px 15px"
            v-for="(items,indexs) in item.order_goods"
          >
            <div>
              <img class="img_pro" :src="items.goods_img">
            </div>
            <div class="flex-column-space-between right">
              <div>
                <div class="newline">{{items.goods_name}}</div>
                <div class="fz12 fc-grey pdtb-5">{{items.goods_num}}</div>
              </div>
              <div class="fc-fen flex-jc-between">
                <span>{{items.goods_price}}</span>
                <div
                  class="fc-fen text-c border-fen"
                  style=" border-radius: 3px; padding:2px 4px"
                  v-show="item.order_status==4"
                  @click.stop="toComment(items)"
                ></div>
              </div>
            </div>
          </div>
          <div class="price_box">
            <div class="item-total">{{item.total_price}}</div>
            <div>:</div>
          </div>
          <div class="btn_box pd-10" v-show="item.order_status==0">
            <span class="fc-fen text-c border-fen" @click.stop="toPay(item.id)"></span>
            <span class="fc-grey text-c border" @click.stop="onshowmodel(item.id)"></span>
          </div>
          <div class="btn_box pd-10" v-show="item.order_status==2">
            <span class="fc-fen text-c border-fen" @click.stop="confirm(item.id)"></span>
          </div>
          <div class="btn_box pd-10" v-show="item.order_status==3">
            <span class="fc-fen text-c border-fen" @click.stop="confirm(item.id)"></span>
          </div>
          <div class="btn_box pd-10" v-show="item.order_status==4">
            <span class="fc-grey text-c border" @click.stop="del(item.id)"></span>
          </div>
          <div class="btn_box pd-10" v-show="item.order_status==5">
            <span class="fc-grey text-c border" @click.stop="del(item.id)"></span>
          </div>
        </div>
      </div>
    </div>

    <van-popup v-model="showmodel" position="bottom" :close-on-click-overlay="false">
      <div class="text-c position title">
        
        <div class="closeicon">
          <van-icon name="close" @click="onshowmodel"/>
        </div>
      </div>
      <div class="pd-lr-15">
        <div class="pd-tb-15">,</div>
        <van-radio-group v-model="radio" @change="checkedName">
          <div
            class="flex-jc-between mar-b-10"
            @click="radio = index"
            v-for="(item,index) in canceltext"
            :key="index"
          >
            <span>{{index+1}}{{item.text}}</span>
            <van-radio :name="index" checked-color="#FE4171"></van-radio>
          </div>
        </van-radio-group>
      </div>
      <div class="pd-15">
        <div class="btn text-c" @click="cancelOrder()"></div>
      </div>
    </van-popup>

    <van-dialog v-model="show" show-cancel-button :before-close="beforeClose">
      <div class="text-c pd-15"></div>
    </van-dialog>
  </div>
</template>

<script>
import { Dialog, Toast } from "vant";
import wx from "weixin-js-sdk";
export default {
  data() {
    return {
      typenum: this.$route.query.id,
      radio: 0,
      isdianzan: false,
      orderlist: [],
      order_id: "",
      cancel_remark: "/",
      canceltext: [
        { id: 1, text: "/" },
        { id: 2, text: "" },
        { id: 3, text: "" },
        { id: 4, text: "" }
      ],
      showmodel: false,
      show: false
    };
  },
  created() {
    console.log(this.$route.query.id);
    this.getData();
  },
  methods: {
    getData() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/order/my", {
          params: {
            type: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "order/my");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.orderlist = resdata.data;
            // for (let v of resdata.data) {
            //   this.order_id = v.id;
            // }
            Toast.clear();
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
    },
    // 
    // 
    orderStatus1() {
      this.typenum = 1;

      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/order/my", {
          params: {
            type: 1
          }
        })
        .then(res => {
          console.log(res.data, "order/my");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.orderlist = resdata.data;

            Toast.clear();
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
    },
    // 
    orderStatus2() {
      this.typenum = 2;

      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/order/my", {
          params: {
              type: 2
          }
        })
        .then(res => {
          console.log(res.data, "order/my");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.orderlist = resdata.data;

            Toast.clear();
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
    },
    // 
    orderStatus3() {
      this.typenum = 3;
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/order/my", {
          params: {
              type: 3
          }
        })
        .then(res => {
          console.log(res.data, "order/my");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.orderlist = resdata.data;

            Toast.clear();
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
    },
    // 
    orderStatus4() {
      this.typenum = 4;
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/order/my", {
          params: {
              type: 4
          }
        })
        .then(res => {
          console.log(res.data, "order/my");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.orderlist = resdata.data;

            Toast.clear();
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
    },
    // 
    cancelOrder() {
      Toast.loading({ mask: true, message: "..." });

      let param = new URLSearchParams();
      param.append("order_id", this.order_id);
      param.append("cancel_remark", this.cancel_remark);
      this.axios
        .post(this.API + "/api/v1/order/cancel", param)
        .then(res => {
          console.log(res.data, "order/cancel");
          let resdata = res.data;

          if (resdata.code == 200) {
            Toast(resdata.msg);
            this.getData();

            Toast.clear();
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
      this.showmodel = false;
    },
    onshowmodel(id) {
      console.log(id);
      this.order_id = id;
      if (this.showmodel) {
        this.showmodel = false;
      } else {
        this.showmodel = true;
      }
    },

    // 
    toPay(id) {
      console.log(id);

      let order_id = id;
      let param = new URLSearchParams();
      param.append("order_id", order_id);
      // 
      this.axios
        .get(this.API + "/api/v1/wechat/config", {
          params: {
            store_id: JSON.parse(window.localStorage.getItem("store_id")),
            url: location.href.split("#")[0]
          }
        })
        .then(res => {
          console.log(res.data, "config");
          let resdata = res.data;
          if (resdata.code == 200) {
            wx.config({
              debug: false,
              appId: resdata.data.appId, // 
              nonceStr: resdata.data.nonceStr, // 
              signature: resdata.data.signature, // 1
              timestamp: resdata.data.timestamp, // 
              url: resdata.data.url,
              jsApiList: ["chooseWXPay", "translateVoice"]
            });
            this.axios.post(this.API + "/api/v1/order/pay", param).then(res => {
              let resdata = res.data;
              console.log();
              if (resdata.code == 200) {
                wx.chooseWXPay({
                  timestamp: resdata.data.timeStamp, // 
                  nonceStr: resdata.data.nonceStr, //  32 
                  package: resdata.data.package, // prepay_idprepay_id=\*\*\*
                  signType: resdata.data.signType, // 'SHA1''MD5'
                  paySign: resdata.data.paySign, // 
                  success: function(res) {
                    // 
                    Toast("");
                    window.location.href = `${location.origin}/me`;
                  }
                });
              } else {
                Toast.clear();
                Toast(resdata.msg);
              }
            });
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
    },
    checkedName(res) {
      console.log(res);
      if (res == 0) {
        this.cancel_remark = "/";
      } else if (res == 1) {
        this.cancel_remark = "";
      } else if (res == 2) {
        this.cancel_remark = "";
      } else {
        this.cancel_remark = "";
      }
    },

    // 
    confirm(id) {
      Toast.loading({ mask: true, message: "..." });

      let param = new URLSearchParams();
      param.append("order_id", id);

      this.axios
        .post(this.API + "/api/v1/order/confirm", param)
        .then(res => {
          console.log(res.data, "order/confirm");
          let resdata = res.data;
          this.typenum == 3;
          if (resdata.code == 200) {
              Toast(resdata.msg);
              this.orderStatus3();
            Toast.clear();
          } else if (resdata.code == 40005) {
            this.$router.push({
              path: "/"
            });
          } else {
            Toast(resdata.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("");
        });
    },

    // 
    toDetail(id) {
      this.$router.push({
        path: "/orderDetail/",
        query: {
          id: id
        }
      });
    },
    // 
    toComment(items) {
      console.log(items);
      this.$router.push({
        path: "/comment/",
        query: {
          good: items
        }
      });
    },
    // 
    beforeClose(action, done) {
      if (action === "confirm") {
        Toast.loading({ mask: true, message: "..." });
        let param = new URLSearchParams();
        param.append("order_id", this.order_id);
        this.axios
          .post(this.API + "/api/v1/order/del", param)
          .then(res => {
            console.log(res.data, "order/del");
            let resdata = res.data;
            if (resdata.code == 200) {
              Toast.clear();
              this.orderStatus4();
            } else if (resdata.code == 40005) {
              this.$router.push({
                path: "/"
              });
            } else {
              Toast(resdata.msg);
            }
          })
          .catch(error => {
            Toast.clear();
            Toast("");
          });
        setTimeout(done, 500);
      } else {
        done();
      }
    },

    del(id) {
      this.show = true;
      this.order_id = id;
      console.log(id);
    }
  }
};
</script>



<style scoped>
.nav {
  padding: 15px 10px;
}
.nav > div {
  width: 64px;
  height: 25px;
  line-height: 25px;
  display: inline-block;
  box-shadow: 0 1px 7px 1px #e8e9ea;
  border-radius: 20px;
  color: #6c6c6c;
  text-align: center;
  box-sizing: border-box;
  font-size: 13px;
  margin-right: 7px;
}
.nav .selected {
  background: linear-gradient(90deg, #fe4171, #ff5491);
  color: #fff;
}
.pd-10 {
  padding: 15px 10px;
}
.pdtb-5 {
  padding: 5px 0;
}
.fz12 {
  font-size: 12px;
}

.fsz12 {
  font-size: 12px;
}

.daifu-box {
  width: 290px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 1px 2px 8px 0px rgba(207, 206, 206, 0.5);
}
.daifa-box {
  width: 290px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 1px 2px 8px 0px rgba(207, 206, 206, 0.5);
}
.img_pro {
  width: 60px;
  height: 65px;
  border-radius: 3px;
}
.right {
  width: 195px;
  padding: 5px 10px;
}
.price_box {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  padding: 0 10px;
  font-size: 12px;
}
.item-total {
  margin-top: 2px;
  font-size: 12px;
}
.btn_box {
  display: flex;
  flex-direction: row-reverse;
}
.btn_box span {
  width: 80px;
  height: 30px;
  margin-left: 10px;
  line-height: 30px;
  border-radius: 5px;
}
.tag .dot {
  position: absolute;
  top: 0;
  right: 0;
}

.list {
  padding: 14px;
}
.model {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
.model {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.btn {
  height: 30px;
  line-height: 30px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #fe4171, #fe4171 100%);
}

.title .closeicon {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 20px;
}
.title {
  font-size: 16px;
  padding-top: 15px;
}
</style>


